import { Col, Form, Row, Tag } from "antd";
import moment from "moment";
import { useCallback, useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import Swal from "sweetalert2";
import { Fn } from "../../../../helper/function";
import { TaskProjectManageService } from "../../../../services/taskService";
import { MyFormItemDateTimePicker, MyFormItemInput, MyFormItemSelect, MyFormItemTextArea } from "../../../core/Input";
import { Box, MyBox } from "../../../core/MyBox";
import { MyButton } from "../../../core/MyButton";
import SearchComponent from "../../../core/SearchComponent";

export default function ModifyComponent() {
    const options = [
        {
            key: 0,
            label: "Mới",
            value: 0
        },
        {
            key: 1,
            label: "Đang hoạt động",
            value: 1
        },
        {
            key: 2,
            label: "Tạm ngưng",
            value: 2
        },
        {
            key: 3,
            label: "Đã hoàn thành",
            value: 3
        },
        {
            key: -1,
            label: "Hủy",
            value: -1
        }
    ]

    const navigate = useNavigate();
    const [isLoading, setIsLoading] = useState(false);
    const [name, setName] = useState('');
    const [content, setContent] = useState('');
    const [status, setStatus] = useState(0);
    const [parentKey, setParentKey] = useState('');
    const [parentName, setParentName] = useState('');
    const [tag, setTag] = useState('');
    const [createdAt, setCreatedAt] = useState(moment());
    const [updatedAt, setUpdatedAt] = useState(moment());
    const [dataParentSearch, setDataParentSearch] = useState([]);

    var keyData = useParams().key;

    const loadData = useCallback(() => {
        if (keyData !== undefined) {
            setIsLoading(true);
            TaskProjectManageService.GetDetail(keyData, (data: any) => {
                const row = data.data.data;
                setName(row.name);
                setContent(row.content);
                setTag(row.tag.toString());
                setStatus(row.status);
                setCreatedAt(moment(row.createdAt));
                setUpdatedAt(moment(row.updatedAt));
                let parentProject = row.parentProject;
                if (parentProject !== null) {
                    setParentKey(parentProject.key);
                    setParentName(parentProject.name);
                }

            }, undefined, () => {
                setIsLoading(false);
            })
        }
    }, [keyData]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function OnSearch(data: any) {
        setIsLoading(true);
        TaskProjectManageService.Search(data, (data: any) => {
            setDataParentSearch(data.data.data);
        }, undefined, () => {
            setIsLoading(false);
        })
    }

    function handleSubmitForm() {
        let body = {
            name: name,
            content: content,
            status: status,
            tag: tag.split(","),
            parentKey: parentKey === '' ? null : parentKey
        };

        setIsLoading(true);
        if (keyData === undefined) {
            Add(body);
        } else {
            Edit(body);
        }
    }

    function Add(body: any) {
        TaskProjectManageService.Add(body, (data: any) => {
            Fn.PrintMsgSuccess(data);
            navigate(`/tools/tasks/projects/edit/${data.data.data.key}`)
        }, undefined, () => {
            setIsLoading(false);
        });
    }

    function Edit(body: any) {
        TaskProjectManageService.GetDetail(keyData, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            TaskProjectManageService.Edit(keyData, body, (data: any) => {
                Fn.PrintMsgSuccess(data);
                setIsLoading(false);
            }, () => {
                setIsLoading(false);
            });
        }, () => {
            setIsLoading(false);
        });
    }

    function GenerateTag() {
        const color = ["magenta", "red", "volcano", "orange", "gold", "lime", "green", "cyan", "blue", "geekblue", "purple"];
        return tag.split(",").map((data: any, index: number) => {
            return (
                <Tag key={index} color={color[Math.floor(Math.random() * 11)]}>{data}</Tag>
            );
        })
    }

    function handleDeleteProject() {
        Swal.fire({
            title: 'Bạn có muốn xóa dự án này không?',
            showDenyButton: true,
            confirmButtonText: 'Có',
            denyButtonText: `Không`,
        }).then((result: any) => {
            if (result.isConfirmed) {
                DeleteProject();
            }
        })
    }

    function DeleteProject() {
        setIsLoading(true);
        TaskProjectManageService.GetDetail(keyData, (data: any) => {
            TaskProjectManageService.Delete(keyData, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                setIsLoading(false);
                navigate(`/tools/tasks/projects`);
            }, () => {
                setIsLoading(false);
            })
        }, () => {
            setIsLoading(false);
        })
    }

    return (
        <MyBox
            title="Chi tiết dự án"
            isLoading={isLoading}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <Row gutter={12}>
                    <Col span={8}>
                        <MyFormItemInput
                            readOnly={true}
                            label="Mã dự án"
                            value={keyData}
                            setValue={undefined} />
                    </Col>
                    <Col span={16}>
                        <MyFormItemInput
                            label="Tên dự án"
                            value={name}
                            setValue={setName} />
                    </Col>
                    <Col span={24}>
                        <MyFormItemTextArea
                            label="Nội dung"
                            value={content}
                            setValue={setContent} />
                    </Col>
                    <Col span={12}>
                        <MyFormItemInput
                            label="Tag"
                            value={tag}
                            setValue={setTag} />
                    </Col>
                    <Col span={12}>
                        <Box>
                            {GenerateTag()}
                        </Box>
                    </Col>
                    <Col span={12}>
                        <SearchComponent
                            label="Dự án cha"
                            muityChoose={false}
                            onSearch={OnSearch}
                            resultKey={parentKey}
                            resultName={parentName}
                            setResultKey={setParentKey}
                            setResultName={setParentName}
                            dataSearch={dataParentSearch}
                            isLoading={isLoading} />
                    </Col>
                    <Col span={12}>
                        <MyFormItemSelect
                            label="Trạng thái"
                            options={options}
                            value={status}
                            setValue={setStatus} />
                    </Col>
                    <Col span={12}>
                        <MyFormItemDateTimePicker
                            label="Ngày tạo"
                            readOnly={true}
                            value={createdAt}
                            setValue={setCreatedAt} />
                    </Col>
                    <Col span={12}>
                        <MyFormItemDateTimePicker
                            label="Ngày cập nhật cuối cùng"
                            readOnly={true}
                            value={updatedAt}
                            setValue={setUpdatedAt} />
                    </Col>
                    <Col>
                        <MyButton
                            htmlType="submit"
                            type="success"
                            value="Thay đổi" />
                    </Col>
                    <Col>
                        <Link to={`/tools/tasks/projects/edit/${keyData}/sprint`}>
                            <MyButton
                                type="primary"
                                value="Quản lý sprint" />
                        </Link>
                    </Col>
                    <Col>
                        <MyButton
                            onClick={handleDeleteProject}
                            disabled={keyData === undefined}
                            type="danger"
                            value="Xóa dự án" />
                    </Col>

                    <Col>
                        <Link to="/tools/tasks/projects"><MyButton type="warning" value="Hủy" /></Link>
                    </Col>
                </Row>
            </Form>
        </MyBox>
    );
}